@charset "UTF-8";
/* extends */
.CaseCreate {
  padding: 2rem 4rem;
  /*      */
  /*        */
  /*   ,  horizontal vertical   */
  /*     */ }
  .CaseCreate.loading-container {
    padding-top: 10rem;
    text-align: center; }
  .CaseCreate ::-webkit-scrollbar {
    -webkit-appearance: none; }
  .CaseCreate ::-webkit-scrollbar-track-piece {
    background-color: #e7e7e7; }
  .CaseCreate ::-webkit-scrollbar:horizontal {
    height: 12px;
    border-radius: 10px; }
  .CaseCreate ::-webkit-scrollbar-thumb {
    background-color: #d1d1d1;
    border-radius: 10px; }
  .CaseCreate .header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 1.0rem; }
    .CaseCreate .header .case-number {
      font-size: 1.8rem;
      font-weight: 700;
      letter-spacing: 1px; }
    .CaseCreate .header .edit-save-delete-createPDF-button {
      user-select: none;
      display: flex; }
      .CaseCreate .header .edit-save-delete-createPDF-button .button {
        user-select: none;
        color: #2b536b;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        transition: all 0.3s;
        padding: 0.2rem;
        font-size: 1rem; }
        .CaseCreate .header .edit-save-delete-createPDF-button .button:not(:last-child) {
          margin-right: 0.75rem; }
        .CaseCreate .header .edit-save-delete-createPDF-button .button:hover {
          color: #417da2;
          cursor: pointer; }
        .CaseCreate .header .edit-save-delete-createPDF-button .button .icon {
          user-select: none;
          color: inherit;
          font-size: 1.25em; }
        .CaseCreate .header .edit-save-delete-createPDF-button .button .label {
          user-select: none;
          color: inherit;
          font-size: 0.7em; }
  .CaseCreate .main .record-button-wrapper {
    padding: 0.5rem 1rem;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    background-color: #e9ecef;
    border-top-left-radius: 2px;
    border-top-right-radius: 2px; }
    .CaseCreate .main .record-button-wrapper .record, .CaseCreate .main .record-button-wrapper .add-button {
      font-size: 0.7rem;
      padding: 0.3rem 0.6rem;
      border-radius: 8px;
      transition: all 0.2s; }
      .CaseCreate .main .record-button-wrapper .record:hover, .CaseCreate .main .record-button-wrapper .add-button:hover {
        background-color: #518489;
        cursor: pointer;
        color: white; }
    .CaseCreate .main .record-button-wrapper .record {
      padding-left: 2rem;
      padding-right: 2rem;
      border: 1px solid #518489;
      color: inherit; }
      .CaseCreate .main .record-button-wrapper .record:hover {
        text-decoration: none !important; }
      .CaseCreate .main .record-button-wrapper .record:not(:last-child) {
        margin-right: 0.4rem; }
      .CaseCreate .main .record-button-wrapper .record:hover {
        border-color: #68a0a5; }
      .CaseCreate .main .record-button-wrapper .record.active {
        background-color: #2b536b;
        border-color: #2b536b;
        color: white; }
    .CaseCreate .main .record-button-wrapper .add-button {
      margin-right: 0.5rem;
      border: 1px solid #adb5bd;
      background-color: #adb5bd;
      color: black; }
      .CaseCreate .main .record-button-wrapper .add-button:hover {
        background-color: #ced4da;
        color: #495057;
        border-color: #ced4da; }
  .CaseCreate .input-form-container {
    padding: 1rem;
    background-color: white; }
    .CaseCreate .input-form-container .submit-button {
      margin: 2rem 0;
      text-align: center; }
      .CaseCreate .input-form-container .submit-button button {
        font-size: 1.2rem;
        font-weight: 700;
        letter-spacing: 2px;
        background-color: #2b536b;
        padding: 0.5rem 8rem; }
        .CaseCreate .input-form-container .submit-button button:hover {
          background-color: #244559; }
        .CaseCreate .input-form-container .submit-button button:active {
          background-color: #1c3747; }
